import React from 'react'

const Paginacion = props =>{
    return(
        <div className="contenedor-botones">
            <button className="boton-direccion" onClick={props.paginaAnterior} type="button">&larr; Anterior</button>
            <button className="boton-direccion" onClick={props.paginaSiguiente} type="button">Siguiente &rarr;</button>
        </div>
    )
}

export default Paginacion;